/* eslint-disable */
import React, { useEffect, useState, useRef } from "react";
import "./TwoPollBreaker.css";

import Clickable from "../base components/Clickable/Clickable";
import Draggable from "../base components/Draggable/Draggable";

const BreakerPoll = ({ id, label, data, register, onMove, top, left }) => {
  const compRef = useRef(null);

  useEffect(() => { }, []);

  const [state, setState] = useState([
    { id: `clickable-1-breaker-${id}`,label, clicked: false },
    { id: `clickable-2-breaker-${id}`,label, clicked: false },
    { id: `clickable-3-breaker-${id}`,label, clicked: false },
    { id: `clickable-4-breaker-${id}`,label, clicked: false }
  ]);

  const [click1, setClick1] = useState(null);
  const [click2, setClick2] = useState(null);
  const [click3, setClick3] = useState(null);
  const [click4, setClick4] = useState(null);

  useEffect(() => {
    register(state);
  }, [state]);

  useEffect(() => {
    if (data[`clickable-1-breaker-${id}`] !== undefined) {
      setClick1(data[`clickable-1-breaker-${id}`]);
      setClick2(data[`clickable-2-breaker-${id}`]);
      setClick3(data[`clickable-3-breaker-${id}`]);
      setClick4(data[`clickable-4-breaker-${id}`]);
    } else {
      setClick1(state[0]);
      setClick2(state[1]);
      setClick3(state[2]);
      setClick4(state[3]);
    }
  }, [data,state]);

  if (click1 && click2) {
    return (
      <Draggable id={id} onMove={onMove} top={top} left={left}>
        <div id={`breaker-${id}`} className="breaker" ref={compRef}>
          <div class="row">
          <Clickable
            data={{ id: click1.id, label: ['L'], clicked: click1.clicked }}
            style={{ position: 'relative', top: '-11px' }}
          />
          <Clickable
            data={{ id: click3.id, label: ['N'], clicked: click3.clicked }}
            style={{ position: 'relative', top: '-11px' }}
          />
          </div>
          
          <div id={`breaker-${id}`} style={{ color: 'white', textAlign: 'center', fontSize:'1vw'  }}>{label}</div>
          <div class="row">
          <Clickable
            data={{ id: click2.id, label: ['L'], clicked: click2.clicked }}
            style={{ position: 'relative', bottom: '-11px' }}
          />
          <Clickable
            data={{ id: click4.id, label: ['N'], clicked: click4.clicked }}
            style={{ position: 'relative', bottom: '-11px' }}
          />
          </div>
        </div>
      </Draggable>
    );

  }
  return null;
  // return (
  //   <Draggable id={id} onMove={onMove} top={top} left={left}>
  //     <div id={`breaker-${id}`} className="breaker" ref={compRef}>
  //       <Clickable
  //         data={{ id: id, label: ['L', '1'], clicked: false }}
  //         style={{ position: 'relative', top: '-9px' }}
  //       />
  //       <div id={`breaker-${id}`} style={{ color: 'white' }}>{label}</div>
  //       <Clickable
  //         data={{ id: id, label: ['T', '1'], clicked: false }}
  //         style={{ position: 'relative', bottom: '-9px' }}
  //       />
  //     </div>
  //   </Draggable>
  // );
};

export default BreakerPoll;
