/* eslint-disable */
import React, { useEffect, useState } from "react";
import "./SelectorSwitch.css";

import Clickable from "../base components/Clickable/Clickable";
import Draggable from "../base components/Draggable/Draggable";

const points = {
  p1: ["L1"],
  p2: ["L2"],
  p3: ["L3"],
  p4: ["T1"],
  p5: ["T2"],
  p6: ["T3"],
  p7: ["T4"],
  p8: ["T5"],
  p9: ["T6"]
};

const SelectorSwitch = ({
  id,
  label,
  data,
  register,
  type = "selectorswitch",
  color = "#ab6393",
  onMove,
  pointLabels = points, top, left
}) => {
  const [state, setState] = useState([
    { id: `clickable-L1-${type}-${id}`, label, clicked: false },
    { id: `clickable-L2-${type}-${id}`, label, clicked: false },
    { id: `clickable-L3-${type}-${id}`, label, clicked: false },
    { id: `clickable-T1-${type}-${id}`, label, clicked: false },
    { id: `clickable-T2-${type}-${id}`, label, clicked: false },
    { id: `clickable-T3-${type}-${id}`, label, clicked: false },
    { id: `clickable-T4-${type}-${id}`, label, clicked: false },
    { id: `clickable-T5-${type}-${id}`, label, clicked: false },
    { id: `clickable-T6-${type}-${id}`, label, clicked: false }
  ]);

  const [click1, setClick1] = useState(null);
  const [click2, setClick2] = useState(null);
  const [click3, setClick3] = useState(null);
  const [click4, setClick4] = useState(null);
  const [click5, setClick5] = useState(null);
  const [click6, setClick6] = useState(null);
  const [click7, setClick7] = useState(null);
  const [click8, setClick8] = useState(null);
  const [click9, setClick9] = useState(null);

  useEffect(() => {
    register(state);
  }, [state]);

  useEffect(() => {
    if (data[`clickable-1-${type}-${id}`]) {
      setClick1(data[`clickable-L1-${type}-${id}`]);
      setClick2(data[`clickable-L2-${type}-${id}`]);
      setClick3(data[`clickable-L3-${type}-${id}`]);
      setClick4(data[`clickable-T1-${type}-${id}`]);
      setClick5(data[`clickable-T2-${type}-${id}`]);
      setClick6(data[`clickable-T3-${type}-${id}`]);
      setClick7(data[`clickable-T4-${type}-${id}`]);
      setClick8(data[`clickable-T5-${type}-${id}`]);
      setClick9(data[`clickable-T6-${type}-${id}`]);
    } else {
      setClick1(state[0]);
      setClick2(state[1]);
      setClick3(state[2]);
      setClick4(state[3]);
      setClick5(state[4]);
      setClick6(state[5]);
      setClick7(state[6]);
      setClick8(state[7]);
      setClick9(state[8]);
    }
  }, [data]);

  if (click1 && click2) {
    return (
      <Draggable id={id} onMove={onMove} top={top} left={left}>
        <div
          id={`selectorswitch-${id}`}
          className="selectorswitch-container"
          style={{ backgroundColor: color }}
        >
          <div id={`selectorswitch-${id}`} className="selectorswitch-row-top">
            <Clickable
              data={{
                id: click1.id,
                label: pointLabels.p1,
                clicked: click1.clicked
              }}
            />
            <Clickable
              data={{
                id: click2.id,
                label: pointLabels.p2,
                clicked: click2.clicked
              }}
            />
            <Clickable
              data={{
                id: click3.id,
                label: pointLabels.p3,
                clicked: click3.clicked
              }}
            />
            <div
              id={`selectorswitch-${id}`}
              className="selectorswitch-row-top-space-holder"
            />
          </div>
          <div
            id={`selectorswitch-${id}`}
            className="selectorswitch-row-middle"
            style={{fontSize:'1vw'}}
          >
            {label}
          </div>
          <div
            id={`selectorswitch-${id}`}
            className="selectorswitch-row-bottom"
          >
            <Clickable
              data={{
                id: click4.id,
                label: pointLabels.p4,
                clicked: click4.clicked
              }}
            />
            <Clickable
              data={{
                id: click5.id,
                label: pointLabels.p5,
                clicked: click5.clicked
              }}
            />
            <Clickable
              data={{
                id: click6.id,
                label: pointLabels.p6,
                clicked: click6.clicked
              }}
            />
            <Clickable
              data={{
                id: click7.id,
                label: pointLabels.p7,
                clicked: click7.clicked
              }}
            />
            <Clickable
              data={{
                id: click8.id,
                label: pointLabels.p8,
                clicked: click8.clicked
              }}
            />
            <Clickable
              data={{
                id: click9.id,
                label: pointLabels.p9,
                clicked: click9.clicked
              }}
            />
          </div>
        </div>
      </Draggable>
    );
  }
  return null;
};

export default SelectorSwitch;
