import React from "react";
import TenPointBase from "../base components/TenPointBase/TenPointBase";

const OverloadRelay = ({ id, label, data, register, onMove, top, left }) => {
  return (
    <TenPointBase
      id={id}
      label={label}
      data={data}
      register={register}
      onMove={onMove}
      top={top} left={left}
    />
  );
};

export default OverloadRelay;
