import React, { useEffect, useState, useRef } from "react";
import Breaker from "../electrical-components/Breaker/Breaker";
import BreakerPoll from "../electrical-components/BreakerPoll/TwoPollBreaker";
import OverloadRelay from "../electrical-components/OverloadRelay/OverloadRelay";
import Contactor from "../electrical-components/Contactor/Contactor";
import OnDelay from "../electrical-components/OnDelay/OnDelay";
import AutoTransformer from "../electrical-components/AutoTransformer/AutoTransformer";
import StartButton from "../electrical-components/StartButton/StartButton";
import StopButton from "../electrical-components/StopButton/StopButton";
import Resistor from "../electrical-components/Resistor/Resistor";
import ThreePhaseMotor from "../electrical-components/ThreePhaseMotor/ThreePhaseMotor";
import SinglePhaseMotor from "../electrical-components/OnePhaseMotor/OnePhaseMotor";
import SelectorSwitch from "../electrical-components/SelectorSwitch/SelectorSwitch";
import MotorProtectionCircuitBreaker from "../electrical-components/MPCB/MotorProtectionCircuitBreaker";
import Source from "../electrical-components/base components/Source/Source";
import OffDelay from "../electrical-components/OffDelay/OffDelay";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import "./Designer.css";
import trash from "./delete.svg";
import breaker from "../../assets/breaker.png";
import breakerpoll from "../../assets/2PollBreaker.png";
import resistor from "../../assets/Resistor.png";
import autoTransformer from "../../assets/Auto Transformer.png";
import contactor from "../../assets/Contactor.png";
import motorProtectionCircuitBreaker from "../../assets/Motor Protection Circuit Breaker.png";
import offDelay from "../../assets/Off Delay.png";
import onDelay from "../../assets/On Delay.png";
import singlePhaseMotor from "../../assets/One phase motor.png";
import overloadRelay from "../../assets/Overload relay.png";
import selectorSwitch from "../../assets/Selector Switch.png";
import start from "../../assets/Start.png";
import stop from "../../assets/Stop.png";
import threePhaseMotor from "../../assets/Three Phase Motor.png";
import { withFirebase } from "../Firebase";
import PlainDraggable from "plain-draggable";
import { useToasts } from "react-toast-notifications";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";

// Material Components
import {
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Collapse,
  ListItemSecondaryAction,
  IconButton
} from "@material-ui/core";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import { ExpandMore, ExpandLess } from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";

import Navigation from "../Layout/Navigation/Navigation";
import ToolTray from "../Layout/ToolTray/ToolTray";

const useStyles = makeStyles(theme => ({
  root: {
    height: "200%"
  },
  dialog: {
    maxHeight: 1000
  }
}));

const useListStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    maxWidth: "720",
    backgroundColor: theme.palette.background.paper
  },
  nested: {
    paddingLeft: theme.spacing(4)
  },
  list: {
    width: 250
  },
  componentsDrawer: {
    width: 250,
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center"
  },
  fullList: {
    width: "auto"
  },
  listItemText: {
    fontSize: "1.2em"
  }
}));

const LeaderLine = require("../electrical-components/Line/leader-line");

const DesignerBase = props => {
  const classes = useStyles();
  const listClasses = useListStyles();

  let [objects1, setObjects1] = useState({});
  const [open, setOpen] = React.useState(false);
  const [remove, setRemove] = React.useState(false);
  const [lines, setLines] = useState([]);
  const [source, setSource] = useState("");
  const [target, setTarget] = useState("");
  const [connections, setConnections] = useState([]);
  const [toRemove, setToRemove] = useState("");
  const [clickables, setClickables] = useState({});
  const [wireColor, setWireColor] = useState("#403F3F");
  const [label, setLabel] = useState("");
  const [type] = useState();
  const [detail] = useState();
  let refDetail = useRef(detail);
  let refValue = useRef(wireColor);
  let refType = useRef(type);
  let refLabel = useRef(label);
  const [ecomponents, setEcomponents] = useState([]);
  const [problemName, setProblemName] = useState("");
  const [problemDescription, setProblemDescription] = useState("");
  const [cNames, setCNames] = useState([]);
  const [loadMemo, setLoadMemo] = useState(false);
  let refNames = useRef(cNames);
  let obj = JSON.parse(sessionStorage.getItem("auth"));
  const [openReset, setOpenReset] = React.useState(false);
  const [expandList, setExpandList] = useState(false);

  const [left, setLeft] = useState(false);
  const [right, setRight] = useState(false);
  const [addDescription, setAddDescription] = React.useState(false);
  const toggleDrawer = (side, open) => event => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    if (side === "left") {
      setLeft(open);
    }
    if (side === "right") {
      setRight(open);
    }
  };

  const modules = {
    toolbar: [
      [{ header: [1, 2, false] }],
      ["bold", "italic", "underline", "strike", "blockquote"],
      [
        { list: "ordered" },
        { list: "bullet" },
        { indent: "-1" },
        { indent: "+1" }
      ],
      ["link", "image"],
      ["clean"]
    ]
  };

  const formats = [
    "header",
    "bold",
    "italic",
    "underline",
    "strike",
    "blockquote",
    "list",
    "bullet",
    "indent",
    "link",
    "image"
  ];

  const handleListItemClick = () => {
    setExpandList(!expandList);
  };

  const draw = (start, end, options) => {
    return new LeaderLine(
      LeaderLine.pointAnchor(start),
      LeaderLine.pointAnchor(end),
      options
    );
  };

  const checkClass = (styleClasses, styleClass) => {
    return styleClasses.split(" ").includes(styleClass);
  };

  const handleOnClick = e => {
    setConnectionPoint(e);
  };

  const setClickedById = (id, bool) => {
    setClickables(prev => {
      const obj = prev;
      obj[id].clicked = bool;
      return obj;
    });
  };

  const setConnectionPoint = e => {
    if (e && e.target && isClickable(e.target.id)) {
      const { id, className } = e.target;
      if (checkClass(className, "connected")) {
        if (refValue.current === "orange") {
          setConnections(prevConnection => {
            return prevConnection.filter(connection => {
              const points = connection.split(",");
              const shouldRemain = points[1] !== id && points[2] !== id;
              if (!shouldRemain) {
                const id = connection.split(",")[0];
                const id1 = connection.split(",")[1];
                const id2 = connection.split(",")[2];
                document.getElementById(id1).className = "clickable";
                document.getElementById(id2).className = "clickable";
                setLines(lines =>
                  lines.filter(line => {
                    if (parseInt(id) === line._id) {
                      line.remove();
                      return false;
                    }
                    return true;
                  })
                );
                setClickedById(points[1], false);
                setClickedById(points[2], false);
              }
              return shouldRemain;
            });
          });
          return;
        }
      }
      setSource(prev => {
        if (prev) {
          if (prev === id) {
            setClickedById(id, false);
            return "";
          }
          setTarget(id);
          return prev;
        }
        setClickedById(id, true);
        return id;
      });
    } else {
      if (remove === true) {
        let a = e.target.id.split("-");
        removeComponent(a);
        //
      }
    }
  };

  const handleDescriptionClose = () => {
    setAddDescription(false);
  };

  const handleDescriptionChange = e => {
    setProblemDescription(e);
  };

  const handleDescriptionOpen = () => {
    setAddDescription(true);
  };

  useEffect(() => {
    if (!toRemove) {
      return;
    }
    const id = toRemove.split(",")[0];
    const id1 = toRemove.split(",")[1];
    const id2 = toRemove.split(",")[2];

    document.getElementById(id1).className = "clickable";
    document.getElementById(id2).className = "clickable";
    setLines(lines =>
      lines.filter(line => {
        if (parseInt(id) === line._id) {
          line.remove();
          return false;
        }
        return true;
      })
    );
    setToRemove("");
  }, [toRemove]);

  useEffect(() => {
    if (source && target) {
      if (refValue.current !== "orange") {
        const tempSource = document.getElementById(source);
        setClickedById(source, false);
        tempSource.className = "clickable connected";
        const tempTarget = document.getElementById(target);
        setClickedById(target, false);
        tempTarget.className = "clickable connected";
        if (connections.length !== 0) {
          let found = false;
          connections.forEach(connection => {
            let connect1 = connection.split(",")[1];
            let connect2 = connection.split(",")[2];
            if (
              (source === connect1 && target === connect2) ||
              (target === connect1 && source === connect2)
            ) {
              found = true;
              addToast("Connection already exists between these 2 points", {
                autoDismiss: true,
                appearance: "warning"
              });
            }
          });
          if (!found) {
            const line = draw(tempSource, tempTarget, {
              color: wireColor,
              size: 3,
              startPlug: "disc",
              endPlug: "disc"
            });

            setLines(prev => [...prev, line]);

            setConnections(prev => {
              return [
                ...new Set([
                  ...prev,
                  [source, target, line._id].sort().join(",")
                ])
              ];
            });

            setSource("");
            setTarget("");
          }
        } else {
          const line = draw(tempSource, tempTarget, {
            color: wireColor,
            size: 3,
            startPlug: "disc",
            endPlug: "disc"
          });

          setLines(prev => [...prev, line]);

          setConnections(prev => {
            return [
              ...new Set([...prev, [source, target, line._id].sort().join(",")])
            ];
          });

          setSource("");
          setTarget("");
        }
      } else {
        addToast("Please select a wire colour", {
          autoDismiss: true,
          appearance: "warning"
        });
      }
    }
  }, [wireColor, source, target, connections]);

  useEffect(() => {}, [connections]);

  const isClickable = id => {
    if (id) {
      return id.split("-")[0] === "clickable";
    }
    return false;
  };

  const { addToast } = useToasts();

  const updateLines = () => {
    lines.map(line => line.position());
  };

  useEffect(() => {
    document.addEventListener("touchend", handleOnClick, false);
    document.addEventListener("dblclick", handleOnClick, false);

    return () => {
      document.removeEventListener("touchend", handleOnClick, false);
      document.removeEventListener("dblclick", handleOnClick, false);
    };
  });

  useEffect(() => {}, [lines]);

  const registerComp = state => {
    setClickables(prev => {
      const obj = prev;
      for (let i in state) {
        const clickable = state[i];
        obj[clickable.id] = clickable;
      }
      return obj;
    });
  };

  const setReset = () => {
    setOpenReset(true);
  };


  const setWire = color => {
    setRemove(false);
    refValue.current = color;
    setWireColor(color);
  };

  useEffect(() => {}, [clickables]);

  const createComponent = val => {
    setOpen(false);
    let spaceBetween = 0;
    if (ecomponents.length !== 0) {
      spaceBetween = ecomponents[ecomponents.length - 1].left + 200;
    }

    let newComponent = {
      id: val + (ecomponents.length + 1),
      c: val,
      label: refLabel.current,
      top: 200,
      left: spaceBetween
    };
    if (ecomponents.length !== 0) {
      setEcomponents(ecomponents => [...ecomponents, newComponent]);
    } else {
      setEcomponents([newComponent]);
    }
  };

  const loadComponent = (val, id, label, top, left) => {
    setOpen(false);
    let newComponent = { id: id, c: val, label: label, top: top, left: left };
    if (ecomponents.length !== 0) {
      ecomponents.push(newComponent);
      setEcomponents(ecomponents => [...ecomponents, newComponent]);
    } else {
      ecomponents.push(newComponent);
      setEcomponents([newComponent]);
    }
  };

  const CutLine = () => {
    connections.forEach(conn => {
      const id = conn.split(",")[0];
      const id1 = conn.split(",")[1];
      const id2 = conn.split(",")[2];
      document.getElementById(id1).className = "clickable";
      document.getElementById(id2).className = "clickable";
      setLines(lines =>
        lines.filter(line => {
          if (parseInt(id) === line._id) {
            line.remove();
            return false;
          }
          return true;
        })
      );
    });
    connections.length = 0;
    setOpenReset(false);
  };

  const resetpage = () => {
    connections.forEach(conn => {
      const id = conn.split(",")[0];
      const id1 = conn.split(",")[1];
      const id2 = conn.split(",")[2];
      document.getElementById(id1).className = "clickable";
      document.getElementById(id2).className = "clickable";
      setLines(lines =>
        lines.filter(line => {
          if (parseInt(id) === line._id) {
            line.remove();
            return false;
          }
          return true;
        })
      );
    });
    connections.length = 0;
    setEcomponents([]);
    setProblemName("");
  };

  const handleClickOpen = (val, name) => {
    refType.current = val;
    refDetail.current = name;
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleCloseReset = () => {
    setOpenReset(false);
  };


  const handleChange = e => {
    refLabel.current = e.target.value;
    setLabel(e.target.value);
  };

  const onTextChange = event => {
    setProblemName(event.target.value);
  };


  const firebaseSave = event => {
    let res = [];
    let collection = [];
    ecomponents.forEach(component => {
      const el = document.getElementById(component.id);
      const draggable = new PlainDraggable(el);
      collection.push({
        id: component.id,
        c: component.c,
        label: component.label,
        top: draggable.top,
        left: draggable.left
      });
    });
    connections.forEach(connection => {
      let con = connection.split(",");
      // eslint-disable-next-line eqeqeq
      con[0] = lines.filter(f => f._id == con[0])[0].color;
      con = [con[0], con[1], con[2]].join(",");
      res.push(con);
    });
    let problem = [];
    problem.push(res);
    problem.push(collection);
    problem.push({ name: problemName });
    problem.push({ description: problemDescription });
    let user = { ...props.firebase.auth.currentUser }.email;
    let saved = props.firebase.saveProblem(user, problemName, { ...problem });
    if (saved) {
      addToast("Problem Saved", {
        autoDismiss: true,
        appearance: "success"
      });
      firebaseGetAll(obj.user.email);
      loadProblem(
        { ...props.firebase.auth.currentUser }.email + "," + problemName
      );
    }
  };

  // Gets all the problem names from firebase
  const firebaseGetAll = async val => {
    if (val !== undefined) {
      let names = await props.firebase.getProblemNames(val);
      refNames.current = names.lect;
      setCNames(names);
    }
  };

  // Loads a specific problem from firebase
  const loadProblem = async val => {
    CutLine();
    setLoadMemo(true);
    ecomponents.length = 0;
    setEcomponents([]);
    objects1 = await props.firebase.getProblem(val);
    setObjects1(objects1);
    await objects1[1].forEach(async element => {
      refLabel.current = element.label;
      await loadComponent(
        element.c,
        element.id,
        element.label,
        element.top,
        element.left
      );
    });
    setProblemName(val.split(",")[1]);
    if (objects1[3] === undefined) {
      setProblemDescription(undefined);
    } else {
      setProblemDescription(objects1[3].description);
    }
    addToast("The Problem has been loaded successfully", {
      appearance: "success",
      autoDismiss: true
    });
    // alert("Hallo")
    loadLines();
    setLeft(false);
  };

  const loadLines = async () => {
    let lineObject = objects1[0];
    await lineObject.forEach(element => {
      const color = element.split(",")[0];
      const id1 = element.split(",")[1];
      const id2 = element.split(",")[2];
      const tempSource = document.getElementById(id1);
      tempSource.className = "clickable connected";
      const tempTarget = document.getElementById(id2);
      tempTarget.className = "clickable connected";

      const line = draw(tempSource, tempTarget, {
        color: color,
        size: 3,
        startPlug: "disc",
        endPlug: "disc"
      });
      setLines(prev => [...prev, line]);

      setConnections(prev => {
        return [...new Set([...prev, [id1, id2, line._id].sort().join(",")])];
      });
    });
  };

  useEffect(() => {
    if (obj !== null) {
      firebaseGetAll(obj.user.email);
    }
    document.getElementById("blackWire").style.backgroundColor = "black";
  }, []);

  useEffect(() => {
    return () => {
      let element = document.getElementsByTagName("svg");
      while (element[0]) element[0].parentNode.removeChild(element[0]);
    };
  }, []);

  const btnRemove = () => {
    setRemove(true);
  };
  const removeComponent = id => {
    let noRemove = [];
    connections.forEach(connection => {
      let lineid = connection.split(",")[0];
      let connect1 = connection.split(",")[1].split("-")[3];
      let connect2 = connection.split(",")[2].split("-")[3];
      if (connect1 === id[1]) {
        setLines(lines =>
          lines.filter(line => {
            if (parseInt(lineid) === line._id) {
              line.remove();
              return false;
            }
            return true;
          })
        );
      } else if (connect2 === id[1]) {
        setLines(lines =>
          lines.filter(line => {
            if (parseInt(lineid) === line._id) {
              line.remove();
              return false;
            }
            return true;
          })
        );
      } else {
        noRemove.push(connection);
      }
    });
    setConnections(noRemove);
    setEcomponents(components =>
      components.filter(component => component.id !== id[1])
    );
  };

  const deleteProblem = async val => {
    await props.firebase.deleteProblem(val);
    addToast("The Solution has been deleted", {
      appearance: "success",
      autoDismiss: true
    });
    firebaseGetAll(obj.user.email);
    setProblemName("");
  };

  if (obj !== null) {
    if (clickables) {
      return (
        <div className={classes.root}>
          <Navigation
            onMenuIconClick={toggleDrawer("left", true)}
            pageTitle="Build a Scenario"
          />
          <ToolTray
            loadMemo={loadMemo}
            onLoadMemoClick={loadLines}
            problemName={problemName}
            onSaveClick={firebaseSave}
            onTextChange={onTextChange}
            setWire={setWire}
            setReset={setReset}
            btnRemove={btnRemove}
            resetpage={resetpage}
            testSolution={() => {}}
            handleDescriptionOpen={handleDescriptionOpen}
          />
          <Divider />
          <Drawer open={left} onClose={toggleDrawer("left", false)}>
            <div className={listClasses.list} role="presentation">
              <List
                component="nav"
                aria-labelledby="simulation-list"
                className={listClasses.root}
              >
                <ListItem button onClick={handleListItemClick}>
                  <ListItemIcon>
                    <InboxIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary="Problems"
                    classes={{ primary: listClasses.listItemText }}
                  />
                  {expandList ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Divider />
                <Collapse in={expandList} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    {refNames.current !== [] &&
                      refNames.current.map(el => (
                        <ListItem
                          key={el}
                          button
                          onClick={() =>
                            loadProblem(
                              { ...props.firebase.auth.currentUser }.email +
                                "," +
                                el
                            )
                          }
                          className={listClasses.nested}
                        >
                          <ListItemText
                            primary={el}
                            classes={{ primary: listClasses.listItemText }}
                          />
                          <ListItemSecondaryAction>
                            <IconButton edge="end" aria-label="delete">
                              <img
                                src={trash}
                                alt="delete"
                                width="15pt"
                                onClick={() =>
                                  deleteProblem(
                                    { ...props.firebase.auth.currentUser }
                                      .email +
                                      "," +
                                      el
                                  )
                                }
                              />
                            </IconButton>
                          </ListItemSecondaryAction>
                        </ListItem>
                      ))}
                  </List>
                </Collapse>
              </List>
            </div>
          </Drawer>
          <Drawer
            open={right}
            anchor="right"
            onClose={toggleDrawer("right", false)}
          >
            <div className={listClasses.componentsDrawer} role="presentation">
              <span style={{padding:"2%"}}>Click a component to add it</span>
              <img
                src={breaker}
                alt="Breaker"
                onClick={() => handleClickOpen(Breaker, "Breaker")}
                width="100rem"
              />
              <br></br>
              <img
                src={breakerpoll}
                alt="2-Poll Breaker"
                onClick={() => handleClickOpen(BreakerPoll, "2-Poll Breaker")}
                width="100rem"
              />
              <br></br>
              <img
                src={resistor}
                alt="Resistor"
                onClick={() => handleClickOpen(Resistor, "Resistor")}
                width="100rem"
              />
              <br></br>
              <img
                src={autoTransformer}
                alt="Auto Transformer"
                onClick={() =>
                  handleClickOpen(AutoTransformer, "Auto Transformer")
                }
                width="100rem"
              />
              <br></br>
              <img
                src={contactor}
                alt="Contactor"
                onClick={() => handleClickOpen(Contactor, "Contactor")}
                width="150rem"
              />
              <br></br>
              <img
                src={motorProtectionCircuitBreaker}
                alt="Motor Protection Circuit Breaker"
                onClick={() =>
                  handleClickOpen(
                    MotorProtectionCircuitBreaker,
                    "Motor Protection Circuit Breaker"
                  )
                }
                width="100rem"
              />
              <br></br>
              <img
                src={offDelay}
                alt="Off Delay"
                onClick={() => handleClickOpen(OffDelay, "Off Delay")}
                width="100rem"
              />
              <br></br>
              <img
                src={onDelay}
                alt="On Delay"
                onClick={() => handleClickOpen(OnDelay, "On Delay")}
                width="100rem"
              />
              <br></br>
              <img
                src={singlePhaseMotor}
                alt="Single-Phase Motor"
                onClick={() =>
                  handleClickOpen(SinglePhaseMotor, "Single-Phase Motor")
                }
                width="100rem"
              />
              <br></br>
              <img
                src={overloadRelay}
                alt="Overlay Relay"
                onClick={() => handleClickOpen(OverloadRelay, "Overload Relay")}
                width="150rem"
              />
              <br></br>
              <img
                src={selectorSwitch}
                alt="Selector Switch"
                onClick={() =>
                  handleClickOpen(SelectorSwitch, "Selector Switch")
                }
                width="150rem"
              />
              <br></br>
              <img
                src={start}
                alt="Start"
                onClick={() => handleClickOpen(StartButton, "Start Button")}
                width="70rem"
              />
              <br></br>
              <img
                src={stop}
                alt="Stop"
                onClick={() => handleClickOpen(StopButton, "Stop Button")}
                width="70rem"
              />
              <br></br>
              <img
                src={threePhaseMotor}
                alt="Three-Phase Motor"
                onClick={() =>
                  handleClickOpen(ThreePhaseMotor, "Three-Phase Motor")
                }
                width="100rem"
              />
              <br></br>
            </div>
          </Drawer>
          <Source
            id="source1"
            label="1"
            data={clickables}
            color="#403F3F"
            register={registerComp}
            onMove={updateLines}
            left={20}
          />
          <Source
            id="source2"
            label="2"
            data={clickables}
            color="#5071B4"
            register={registerComp}
            onMove={updateLines}
            left={60}
          />
          <Source
            id="source3"
            label="3"
            data={clickables}
            color="#D4423A"
            register={registerComp}
            onMove={updateLines}
            left={100}
          />
          <Source
            id="sourceEarth"
            label="E"
            data={clickables}
            color="#69BD63"
            type="Earth"
            register={registerComp}
            onMove={updateLines}
            left={140}
          />
          <Source
            id="sourceNeutral"
            label="N"
            data={clickables}
            color="#836953"
            type="Neutral"
            register={registerComp}
            onMove={updateLines}
            left={180}
          />
          <div className="toolbar-right-container">
            <div className="hamburger">
              <Button
                variant="outlined"
                style={{ backgroundColor: "#86b59c" }}
                onClick={toggleDrawer("right", true)}
              >
                Components
              </Button>
            </div>
          </div>
          {ecomponents.map(component => {
            let resA;
            // console.log(component.c)
            switch (component.c) {
              default: {
                addToast("Invalid Component", {
                  appearance: "warning",
                  autoDismiss: true
                });
                break;
              }
              case "Auto Transformer": {
                resA = (
                  <AutoTransformer
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Breaker": {
                resA = (
                  <Breaker
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "2-Poll Breaker": {
                resA = (
                  <BreakerPoll
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Contactor": {
                resA = (
                  <Contactor
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Motor Protection Circuit Breaker": {
                resA = (
                  <MotorProtectionCircuitBreaker
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Off Delay": {
                resA = (
                  <OffDelay
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "On Delay": {
                resA = (
                  <OnDelay
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Single-Phase Motor": {
                resA = (
                  <SinglePhaseMotor
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Overload Relay": {
                resA = (
                  <OverloadRelay
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Resistor": {
                resA = (
                  <Resistor
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Selector Switch": {
                resA = (
                  <SelectorSwitch
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Start Button": {
                resA = (
                  <StartButton
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Stop Button": {
                resA = (
                  <StopButton
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
              case "Three-Phase Motor": {
                resA = (
                  <ThreePhaseMotor
                    key={component.id}
                    id={component.id}
                    label={component.label}
                    register={registerComp}
                    data={clickables}
                    onMove={updateLines}
                    onClick={() => removeComponent(component.id)}
                    top={component.top}
                    left={component.left}
                  />
                );
                break;
              }
            }
            return resA;
          })}
          <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="form-dialog-title"
          >
            <DialogTitle id="form-dialog-title">
              Add a new {refDetail.current}
            </DialogTitle>
            <DialogContent>
              <DialogContentText>
                Please enter a name for the new {refDetail.current}.
              </DialogContentText>
              <TextField
                autoFocus
                margin="dense"
                id="name"
                label="Name"
                type="text"
                fullWidth
                onChange={handleChange}
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={handleClose} color="primary">
                Cancel
              </Button>
              <Button
                onClick={() => createComponent(refDetail.current)}
                color="primary"
              >
                Add {refDetail.current}
              </Button>
            </DialogActions>
          </Dialog>
          <Dialog
            open={openReset}
            onClose={handleCloseReset}
            aria-labelledby="form-dialog-title"
          >
            <DialogTitle id="form-dialog-title">
              Reset all connections.
            </DialogTitle>
            <DialogContent>
              <DialogContentText>
                Are you sure you want to reset all the connections?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseReset} color="primary">
                No
              </Button>
              <Button onClick={() => CutLine()} color="primary">
                Yes
              </Button>
            </DialogActions>
          </Dialog>
          <Dialog
            open={addDescription}
            onClose={handleDescriptionClose}
            aria-labelledby="form-dialog-title"
            fullWidth={true}
            maxWidth={"md"}
          >
            <div style={{ height: "50vh" }}>
              <DialogTitle id="form-dialog-title">
                Problem Description
              </DialogTitle>
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  position: "relative"
                }}
              >
                <DialogContent>
                  <DialogContentText>
                    Please enter a description for the problem here.
                  </DialogContentText>
                  <div className={"textDialog"}>
                    <ReactQuill
                      autoFocus
                      margin="dense"
                      id="name"
                      label="Name"
                      type="text"
                      style={{height: "30vh"}}
                      fullWidth
                      modules={modules}
                      formats={formats}
                      value={problemDescription}
                      onChange={handleDescriptionChange}
                    />
                  </div>
                </DialogContent>
              </div>
              <div style={{ position: "absolute", right: "0", bottom: "0" }}>
                <DialogActions>
                  <Button
                    onClick={() => handleDescriptionClose()}
                    color="primary"
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={() => handleDescriptionClose()}
                    color="primary"
                  >
                    Add Description
                  </Button>
                </DialogActions>
              </div>
            </div>
          </Dialog>
        </div>
      );
    }
  } else {
    props.history.push("/student");
    return <div></div>;
  }
};

const Designer = withFirebase(DesignerBase);

export default Designer;
