/* eslint-disable */
import React from 'react';
import TwelvePointBase from '../base components/TwelvePointBase/TwelvePointBase';

const Contactor = ({ id, label, data, register, onMove, top, left }) => {
    return (
        <TwelvePointBase id={id} label={label} data={data} type="contactor" color1="#ffc180" color2="#ffce99" register={register} onMove={onMove} top={top} left={left} />
    )
};

export default Contactor;
