import app from "firebase/app";
import "firebase/auth";
import "firebase/firestore";

const firebaseConfig = {
  apiKey: process.env.REACT_APP_DEV_API_KEY,
  authDomain: process.env.REACT_APP_DEV_AUTH_DOMAIN,
  databaseURL: process.env.REACT_APP_DEV_DATABASE_URL,
  projectId: process.env.REACT_APP_DEV_PROJECT_ID,
  storageBucket: process.env.REACT_APP_DEV_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_DEV_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_DEV_APP_ID,
  measurementId: process.env.REACT_APP_DEV_MEASUREMENT_ID
};

class Firebase {
  constructor() {
    app.initializeApp(firebaseConfig);

    this.auth = app.auth();
    this.db = app.firestore();
  }

  doCreateUserWithEmailAndPassword = (email, password) =>
    this.auth.createUserWithEmailAndPassword(email, password);

  doSignInWithEmailAndPassword = (email, password) =>
    this.auth.signInWithEmailAndPassword(email, password).then(res => sessionStorage.setItem('auth',JSON.stringify(res)));

  doSignOut = () => this.auth.signOut().then(sessionStorage.clear());

  saveProblem = (lecturer, problemName, problem) => {
    if(problemName.includes("/")){
      return this.db
      .collection(lecturer)
      .doc(problemName.replace(/\//g,"|a|"))
      .set(problem)
      .then(() => {return true})
      .catch(err => {return err});
    }
    else
    {
      return this.db
      .collection(lecturer)
      .doc(problemName)
      .set(problem)
      .then(() => {return true})
      .catch(err => {return err});
    }
    
  };

  getProblemNames = async lecturer => {
    let res = {};
    let base = [],
      lect = [];
    let baseSnap = await this.db.collection("default@test.co").get();
    await baseSnap.docs.forEach(element => {
      if(element['id'].includes("|a|"))
      {
        base.push( element['id'].replace(/\|a\|/g,"/") );
      }
      else
      {
        base.push( element['id'] );
      }
    });
    if(lecturer !== ""){
      let lectSnap = await this.db.collection(lecturer).get();
      await lectSnap.docs.forEach(element => {
        if(element['id'].includes("|a|"))
      {
        lect.push( element['id'].replace(/\|a\|/g,"/") );
      }
      else
      {
        lect.push( element['id'] );
      }
      });
    }
    

    res.base = base;
    res.lect = lect;
    return res;
  };

  getProblems = async lecturer => {
    let res = {};
    let base = [],
      lect = [];
    let baseSnap = await this.db.collection("default@test.co").get();
    await baseSnap.docs.forEach(element => {
      base.push({ ...element.data() });
    });
    let lecturerSnap = await this.db.collection(lecturer).get();
    await lecturerSnap.docs.forEach(element => {
      lect.push({ ...element.data() });
    });
    res.base = base;
    res.lect = lect;
    return res;
  };

  getProblem = async name => {
    let prob
    let data = name.split(',');
    if(data[1].includes("/"))
    {
      prob = (await this.db.collection(data[0]).doc(data[1].replace(/\//g,"|a|")).get()).data()
    }
    else{
      prob = (await this.db.collection(data[0]).doc(data[1]).get()).data()
    }
    
    return prob;
  }

  deleteProblem = async name => {
    let data = name.split(",");
    if(data[1].includes("/"))
    {
      return await this.db.collection(data[0]).doc(data[1].replace(/\//g,"|a|")).delete()
    }
    else{
      return await this.db.collection(data[0]).doc(data[1]).delete()
    }
  }
}
export default Firebase;
